<template>
  <div>
    <table class="rank--top-scores" v-if="!disableTop">
      <tbody>
        <tr><td v-for="(item, index) in this.data.slice(0, 3)" :key="item[0]"
          class="rank-title">{{ index + 1 }}: <slot :item="item">{{ item[0] }}</slot></td></tr>
        <tr><td v-for="(item, index) in this.data.slice(0, 3)" :key="item[0]">
          <div class="rank-avatar" :style="{backgroundImage: 'url(' + getAvatar(item[0]) + ')'}"></div>
        </td></tr>
        <tr><td v-for="(item, index) in this.data.slice(0, 3)" :key="item[0]">{{ format(item[1]) }}</td></tr>
      </tbody>
    </table>
    <table class="mdl-data-table mdl-js-data-table rank-table">
      <thead><tr>
        <th class="rank-index">Rank</th>
        <th class="mdl-data-table__cell--non-numeric">{{ header ? header[0] : 'Otaku' }}</th>
        <th class="rank-score">{{ header ? header[1] : 'Mensagens' }}</th>
      </tr></thead>
      <transition-group name="rank" tag="tbody">
        <tr v-for="(item, index) in this.data.slice(disableTop ? 0 : 3)" :key="item[0]" class="rank-item">
          <td>{{ index + 1 + (disableTop ? 0 : 3) }}</td>
          <td class="mdl-data-table__cell--non-numeric">
            <slot :item="item">{{ item[0] }}</slot>
          </td>
          <td>{{ format(item[1], item[0]) }}</td>
        </tr>
      </transition-group>
    </table>
  </div>
</template>

<script>
import avatars from '../assets/reports/avatars.json'

export default {
  props: [
    'data',
    'header',
    'disableTop',
    'formatter'
  ],
  methods: {
    format (data, key) {
      if (this.formatter) {
        return this.formatter(data, key)
      }
      return data && data.toLocaleString('pt')
    },
    getAvatar (username) {
      return avatars[username] || 'https://i.imgur.com/nTQMNMa.png'
    }
  }
}
</script>

<style lang="stylus" scoped>
.rank-table
  width: 100%
  background: rgba(0, 0, 0, 0.1)
  font-size: 0.5em
  table-layout: fixed

.rank-table th
  color: #fff
  font-size: 1em
.rank-table tbody tr:hover
  background: rgba(0, 0, 0, 0.2)

.rank-item
  transition: all 1s
  display: table-row
  margin-right: 10px
.rank-item td
  white-space: normal

.rank-index, .rank-score
  width: 25%

.rank-enter, .rank-leave-active
  opacity: 0
.rank-leave-active
  position: absolute

.rank--top-scores
  width: 100%
  font-size: 0.5em
  margin-bottom: 0.5em
  background: rgba(0, 0, 0, 0.1)
  text-align: center

.rank--top-scores td
  width: 33.33%
  border: 1px solid rgba(0, 0, 0, 0.3)

@media screen and (min-width: 480px)
  .rank--top-scores, .rank-table
    font-size: 0.6em
@media screen and (min-width: 840px)
  .rank--top-scores, .rank-table
    font-size: 0.7em

.rank-avatar
  width: 100%
  padding-top: 100%
  background-size: cover
  background-position: 50% 10%
  background-color: white
</style>



