<template>
  <div class="stats">
    <transition name="slide-fade">
      <div class="loading" v-if="loading">
        Aguarde, carregando estatísticas...
      </div>
    </transition>

    <transition name="slide-fade">
      <div v-if="error" class="error">
        <span class="error--title">Ocorreu um erro:</span> {{ error }}
      </div>
    </transition>

    <div v-if="statistics" class="content">
      <div class="mdl-grid">
        <div class="mdl-cell mdl-cell--3-col mdl-cell--4-col-tablet mdl-cell--4-col-phone stat-panel">
          <span class="stat-title">Rank das mensagens de</span>
          {{ fromDate }} até<br class="mdl-cell--hide-phone mdl-cell--hide-tablet">
          {{ toDate }}
        </div>
        <div class="mdl-cell mdl-cell--3-col mdl-cell--4-col-tablet mdl-cell--4-col-phone stat-panel">
          <AnimatedNumber class="stat-number" :value="statistics.messageCount"></AnimatedNumber>
          <span class="stat-title">mensagens enviadas</span>
        </div>
        <div class="mdl-cell mdl-cell--3-col mdl-cell--4-col-tablet mdl-cell--4-col-phone stat-panel">
          <AnimatedNumber class="stat-number" :value="messageAverage"></AnimatedNumber>
          <span class="stat-title">mensagens por dia em média</span>
        </div>
        <div class="mdl-cell mdl-cell--3-col mdl-cell--4-col-tablet mdl-cell--4-col-phone stat-panel">
          <AnimatedNumber class="stat-number" :value="statistics.uniqueUsers"></AnimatedNumber>
          <span class="stat-title">otakus ativos no {{ nameOfTheThing }}</span>
        </div>
      </div>

      <div class="mdl-grid">
        <div class="mdl-cell mdl-cell--12-col stat-panel">
          <ChartComponent :data="subintervalData" :options="topChartOptions" />
        </div>
      </div>

      <div class="mdl-grid">
        <div class="mdl-cell mdl-cell--12-col stat-columns">
          <div class="stat-panel">
            <h2>Otakus que mais enviaram mensagens</h2>
            <RankComponent :data="statistics.users" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.typos, 3)">
            <h2>Otakus que cometeram mais<br>erros de digitação
            <span class="subheading">De acordo com os dicionários de Português e Inglês do OpenOffice e
            <a href="https://gist.github.com/qgustavor/36b9ff295eff6fa6445066fd1a8e7ba0">essa lista</a>.</span></h2>
            <RankComponent :data="statistics.typos" :header="['Otaku', 'Erros']" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.editedMessages, 3)">
            <h2>Otakus que mais editaram mensagens</h2>
            <RankComponent :data="statistics.editedMessages" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.spoilerUsers, 3)">
            <h2>Otakus que postaram spoilers mais vezes
            <span class="subheading">(usando a tag [spoiler])</span></h2>
            <RankComponent :data="statistics.spoilerUsers" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.fastEdits, 3)">
            <h2>Otakus que foram mais<br>rápidos para editar mensagens</h2>
            <RankComponent :data="statistics.fastEdits" :header="['Mensagem', 'Tempo']" :formatter="timeFormat" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.hostnames, 3)">
            <h2>Sites postados mais vezes</h2>
            <RankComponent :data="statistics.hostnames" :header="['Site', 'Vezes']" disable-top="true" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.smileyUsage, 3)">
            <h2>Smilies usados mais vezes</h2>
            <RankComponent :data="statistics.smileyUsage" :header="['Smiley', 'Vezes']" disable-top="true" />
          </div>

          <div class="stat-panel" v-if="checkLength(statistics.participationScores, 3)">
            <h2>Otakus mais participativos
            <span class="subheading"><a href="https://i.imgur.com/YQx3aw9.png" rel="noreferrer" target="_blank">
              Ganha mais pontos quem fizer mais pessoas responderem.
            </a></span></h2>
            <RankComponent :data="statistics.participationScores" :header="['Otaku', 'Pontos']"  />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.citedUsers, 3)">
            <h2>Otakus mais respondidos
            <span class="subheading">Para responder alguém use @ ou clique no avatar.</span></h2>
            <RankComponent :data="statistics.citedUsers" :header="['Otaku', 'Vezes']" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.messages, 3)">
            <h2>Mensagens mais enviadas</h2>
            <RankComponent :data="statistics.messages" :header="['Mensagem', 'Vezes']" disable-top="true" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.smilies, 3)">
            <h2>Otakus que enviaram smilies mais vezes</h2>
            <RankComponent :data="statistics.smilies" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.noTypos, 3)">
            <h2>Otakus que enviaram mais<br>mensagens sem nenhum erro
            <span class="subheading">Também conhecido como o rank do Professor Pasquale.</span></h2>
            <RankComponent :data="statistics.noTypos" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.images, 3)">
            <h2>Otakus que enviaram imagens mais vezes</h2>
            <RankComponent :data="statistics.images" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.illeism, 3)">
            <h2>Otakus que praticam o ileísmo
            <span class="subheading">(falar de si mesmo na terceira pessoa usando /me)</span></h2>
            <RankComponent :data="statistics.illeism" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.floodCount, 3)">
            <h2>Otakus que enviaram mensagens seguidas mais vezes
            <span class="subheading">Faça isso sem esperar três minutos entre mensagens e irás despertar a fúria!</span></h2>
            <RankComponent :data="statistics.floodCount" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.chatKillers, 3)">
            <h2>Otakus que mataram o shoutbox<br>por mais tempo</h2>
            <RankComponent :data="statistics.chatKillers" :header="['Otaku', 'Tempo']" :formatter="timeFormat" />
          </div>
          <div class="stat-panel" v-if="checkLength(statistics.slowEdits, 3)">
            <h2>Otakus que mais demoraram muito<br>tempo para editar alguma mensagem</h2>
            <RankComponent :data="statistics.slowEdits" :header="['Mensagem', 'Tempo']" :formatter="timeFormat" />
          </div>
          <div class="stat-panel">
            <h2>Mensagens por classe</h2>
            <ChartComponent type="pie" :data="classData" :options="classChartOptions" heightRatio="60" />
          </div>
          <div class="stat-panel">
            <h2>Mensagens por dia da semana</h2>
            <ChartComponent :data="weekData" :options="weekChartOptions" heightRatio="60" />
          </div>
          <div class="stat-panel">
            <h2>Mensagens por hora</h2>
            <ChartComponent :data="hourData" :options="weekChartOptions" heightRatio="60" />
          </div>
        </div>
      </div>

      <div class="mdl-grid" v-if="checkLength(statistics.words, 3)">
        <div class="mdl-cell mdl-cell--12-col stat-panel">
          <h2>Termos mais usados</h2>
          <WordCloud :data="statistics.words" @item-hover="updateWordInfo"/>
          <p class="word-cloud-info">{{ wordCloudInfo }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AnimatedNumber from './AnimatedNumber'
import ChartComponent from './ChartComponent'
import RankComponent from './RankComponent'
import WordCloud from './WordCloud'
import moment from 'moment'

moment.locale('pt')

export default {
  data () {
    return {
      nameOfTheThing: Math.random() > 0.5 ? 'shoutbox' : 'chat',
      loading: false,
      statistics: null,
      error: null,
      wordCloudInfo: ''
    }
  },
  metaInfo () {
    return {
      title: this.statistics && this.statistics.id
        .replace(/full|year-|month-/, '')
        .replace(/(\d+)-(\d+)/, '$2/$1'),
      meta: this.statistics && [{
        name: 'description',
        content: `Rank das mensagens de ${this.fromDate} até ${this.toDate} no chat da UniOtaku.`
      }]
    }
  },
  created () {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData()
  },
  watch: {
    // call again the method if the route changes
    '$route': 'fetchData'
  },
  components: {
    AnimatedNumber,
    ChartComponent,
    RankComponent,
    WordCloud
  },
  computed: {
    fromDate () {
      return moment(this.statistics.from).format('L')
    },
    toDate () {
      return moment(this.statistics.to).format('L')
    },
    messageAverage () {
      return this.statistics.messageCount / moment(this.statistics.to).diff(this.statistics.from, 'days')
    },
    subintervalData () {
      const dataMessages = this.statistics.subintervals
      .sort((a, b) => a[0].localeCompare(b[0]))
      const dataUsers = this.statistics.subintervalUsers
      .sort((a, b) => a[0].localeCompare(b[0]))

      // Disable reactivity:
      const labels = JSON.parse(JSON.stringify(dataMessages.concat(dataUsers)))
      .map(e => e[0]).filter((e, n, a) => a.indexOf(e) === n)
      const valuesMessages = labels.map(e => (dataMessages.find(f => f[0] === e) || [])[1])
      const valuesUsers = labels.map(e => (dataUsers.find(f => f[0] === e) || [])[1])

      return {
        labels,
        datasets: [{
          label: 'Mensagens',
          type: 'line',
          data: valuesMessages,
          backgroundColor: 'rgba(179,181,198,0.2)',
          borderColor: 'rgba(179,181,198,1)',
          pointBackgroundColor: 'rgba(179,181,198,1)',
          pointBorderColor: '#fff',
          pointHoverBackgroundColor: '#fff',
          pointHoverBorderColor: 'rgba(179,181,198,1)',
          yAxisID: 'main-axis',
          cubicInterpolationMode: 'monotone'
        }, {
          label: 'Otakus',
          type: 'bar',
          data: valuesUsers,
          backgroundColor: 'rgba(179,181,198, 0.2)',
          borderColor: '#b3b5c6',
          pointBackgroundColor: 'rgba(179,181,198,1)',
          pointBorderColor: '#fff',
          pointHoverBackgroundColor: '#fff',
          pointHoverBorderColor: 'rgba(179,181,198,1)',
          yAxisID: 'alt-axis'
        }]
      }
    },
    weekData () {
      const dataWeek = this.statistics.weekDays
      .sort((a, b) => a[0] - b[0])
      const labels = moment.weekdays()
      const values = dataWeek.map(e => Number(e[1]))

      return {
        labels,
        datasets: [{
          label: 'Mensagens',
          type: 'bar',
          data: values,
          backgroundColor: 'rgba(179,181,198, 0.2)',
          borderColor: '#b3b5c6',
          pointBackgroundColor: 'rgba(179,181,198,1)',
          pointBorderColor: '#fff',
          pointHoverBackgroundColor: '#fff',
          pointHoverBorderColor: 'rgba(179,181,198,1)'
        }]
      }
    },
    hourData () {
      const dataWeek = this.statistics.hours
      .sort((a, b) => a[0] - b[0])
      const labels = dataWeek.map(e => Number(e[0]))
      const values = dataWeek.map(e => Number(e[1]))

      return {
        labels,
        datasets: [{
          label: 'Mensagens',
          type: 'bar',
          data: values,
          backgroundColor: 'rgba(179,181,198, 0.2)',
          borderColor: '#b3b5c6',
          pointBackgroundColor: 'rgba(179,181,198,1)',
          pointBorderColor: '#fff',
          pointHoverBackgroundColor: '#fff',
          pointHoverBorderColor: 'rgba(179,181,198,1)'
        }]
      }
    },
    classData () {
      const dataWeek = this.statistics.classes
      .sort((a, b) => b[1] - a[1])
      const labels = dataWeek.map(e => e[0])
      const values = dataWeek.map(e => Number(e[1]))

      return {
        labels,
        datasets: [{
          label: 'Mensagens',
          type: 'pie',
          data: values,
          backgroundColor: labels.map((value, index) => `hsla(${index * 60 + 120}, 75%, 50%, 0.75)`),
          hoverBackgroundColor: labels.map((value, index) => `hsla(${index * 60 + 120}, 66%, 60%, 0.6)`),
          borderColor: '#b3b5c6'
        }]
      }
    },
    topChartOptions () {
      return {
        legend: {
          display: false
        },
        onClick: this.statistics.id.substr(0, 6) === 'month-' ? undefined : (event, targets) => {
          const labels = targets.map(e => e._model.label).filter(e => e)
          if (labels.length === 0) return

          setTimeout(() => {
            this.$router.push('/' + labels[0].replace('-', '/'))
          }, 100)
        },
        scales: {
          yAxes: [{
            gridLines: { color: 'rgba(255, 255, 255, 0.33)' },
            ticks: {
              fontColor: 'rgba(255, 255, 255, 0.75)',
              beginAtZero: true
            },
            id: 'main-axis'
          }, {
            gridLines: { display: false },
            ticks: {
              fontColor: 'rgba(255, 255, 255, 0.75)',
              beginAtZero: true
            },
            id: 'alt-axis',
            position: 'right'
          }],
          xAxes: [{
            type: 'time',
            barPercentage: 0.5,
            categoryPercentage: 0.5,
            gridLines: { display: false },
            ticks: { fontColor: 'rgba(255, 255, 255, 0.75)' }
          }]
        }
      }
    },
    weekChartOptions () {
      return {
        legend: {
          display: false
        },
        scales: {
          yAxes: [{
            gridLines: { color: 'rgba(255, 255, 255, 0.33)' },
            ticks: {
              beginAtZero: true,
              fontColor: 'rgba(255, 255, 255, 0.75)'
            },
            id: 'main-axis'
          }],
          xAxes: [{
            barPercentage: 0.95,
            categoryPercentage: 1,
            gridLines: { display: false },
            ticks: { fontColor: 'rgba(255, 255, 255, 0.75)' }
          }]
        }
      }
    },
    classChartOptions () {
      return {
        legend: {
          display: true,
          labels: {
            fontColor: 'rgba(255, 255, 255, 0.75)'
          }
        }
      }
    }
  },
  methods: {
    fetchData () {
      this.error = this.post = null
      this.loading = true

      setTimeout(() => {
        window.componentHandler.upgradeDom()
      }, 100)

      const params = this.$route.params
      const path = params.month
      ? 'month-' + params.year + '-' + params.month
      : params.year
      ? 'year-' + params.year
      : 'full'

      return import('../assets/reports/' + path + '.json')
      .then(statistics => { this.statistics = statistics })
      .catch(err => {
        this.error = 'Página não encontrada.\nEscolha outra página usando o menu superior ou visite a página principal.'
        console.log('Informações detalhadas do erro:', err)
      })
      .then(() => { this.loading = false })
    },
    timeFormat (interval) {
      return [
        [Math.floor(interval / 3600), 'h', 'hora', 'horas'],
        [Math.floor(interval / 60) % 60, 'm', 'minuto', 'min'],
        [Math.floor(interval) % 60, 's', 'segundo', 'seg']
      ]
      .filter(e => e[0] > 0)
      .concat(null)
      .slice(0, 2)
      .reduce((first, second) => {
        if (second === null) return `${first[0]} ${first[0] === 1 ? first[2] : first[3]}`
        return `${first[0]}${first[1]} ${second[0]}${second[1]}`
      })
    },
    updateWordInfo (evt) {
      const item = evt[0]
      const bonusFactor = item.split(' ').length
      this.wordCloudInfo = `"${item}" foi usado no ${this.nameOfTheThing} ${Math.round(evt[2] / bonusFactor)} vezes`
    },
    checkLength (arr, len) {
      return arr && arr.length > len
    }
  }
}
</script>

<style lang="stylus" scoped>
.stats
  max-width: 1280px
  margin: 0 auto

.mdl-progress
  width: 75%
  margin: 1em auto

.loading
  position: absolute
  left: 0
  right: 0
  padding: 3em 0
  text-align: center
  background: #46597a

.stat-panel, .error
  background: rgba(0, 0, 0, 0.1)
  padding: 1em
  font-size: 2em
  line-height: 1em
  margin-bottom: 0
  box-shadow: 0 0 0.2em rgba(0, 0, 0, 0.05), 0 0.1em 0.4em rgba(0, 0, 0, 0.1)

  h2
    font-size: 1em
    font-weight: bold
    text-align: center
    margin: 0.25em 0 1em
    line-height: 0.9em

  .subheading
    display: block
    font-weight: normal
    font-size: 0.5em
    line-height: 1.1em
    opacity: 0.75
    margin-top: 0.5em
    margin-bottom: -0.5em
  a
    color: white
    &:hover
      text-decoration: none

.stat-title
  display: block
  font-weight: bold

.stat-number
  display: block
  font-size: 2em
  margin: 0.25em 0

.word-cloud-info
  text-align: center

@media screen and (min-width: 840px)
  .stat-columns
    columns: 2

    .stat-panel
      -webkit-column-break-inside: avoid
      page-break-inside: avoid
      break-inside: avoid
      margin-bottom: 1em

.error
  text-align: center
  white-space: pre
  margin: 1em auto
  line-height: 1.25em

.error--title
  display: block
  font-weight: bolder
  margin: 0.25em

.slide-fade-enter-active
  transition: opacity .3s ease

.slide-fade-leave-active
  transition: opacity .8s cubic-bezier(1.0, 0.5, 0.8, 1.0)

.slide-fade-enter, .slide-fade-leave-to
  opacity: 0
</style>



