<template>
  <!-- Some browsers - like phantom - don't implement toLocaleString -->
  <span>{{ Math.round(animatedValue).toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.') }}</span>
</template>

<script>
import TWEEN from 'tween.js'

export default {
  data () {
    return {
      animatedValue: 0
    }
  },
  props: [
    'value'
  ],
  mounted () {
    this.animate(this.value, 0)
  },
  watch: {
    value (newValue, oldValue) {
      this.animate(newValue, oldValue)
    }
  },
  methods: {
    animate (newValue, oldValue) {
      let vm = this

      function animate (time) {
        window.requestAnimationFrame(animate)
        TWEEN.update(time)
      }

      new TWEEN.Tween({ tweeningNumber: oldValue })
      .easing(TWEEN.Easing.Quadratic.Out)
      .to({ tweeningNumber: newValue }, 600)
      .onUpdate(function () {
        vm.animatedValue = this.tweeningNumber
      })
      .start()

      animate()
    }
  },
  destroyed () {
    window.cancelAnimationFrame(this.animationRef)
  }
}
</script>



