<template>
  <canvas width="100" :height="heightRatio || 33"></canvas>
</template>

<script>
import Chart from 'chart.js'
import { mergeDeep } from '../util.js'
const charts = {}

export default {
  data () {
    return {
      chart: null
    }
  },
  props: [
    'type',
    'data',
    'options',
    'heightRatio'
  ],
  mounted () {
    if (this.chart) this.chart.destroy()
    this.chart = Math.random()
    charts[this.chart] = new Chart(this.$el, {
      type: this.type || 'bar',
      data: this.data,
      options: this.options || {}
    })
  },
  watch: {
    data () {
      const chart = charts[this.chart]
      if (!chart) return
      const newDatasets = this.data.datasets
      const len = newDatasets.length
      for (let i = 0; i < len; i++) {
        chart.data.datasets[i].data = newDatasets[i].data
      }
      chart.data.datasets.splice(len)
      chart.data.labels = this.data.labels
      chart.update()
    },
    options () {
      const chart = charts[this.chart]
      if (!this.chart) return
      mergeDeep(chart.options, this.options)
      chart.update()
    }
  },
  destroyed () {
    const chart = charts[this.chart]
    if (chart) {
      chart.destroy()
      delete charts[this.chart]
    }
  }
}
</script>



