<template>
  <canvas></canvas>
</template>

<style scoped>
canvas {
  width: 100%;
}
</style>

<script>
import WordCloud from 'wordcloud'

export default {
  props: [
    'data',
    'options'
  ],
  mounted () {
    this.processCloud()
  },
  watch: {
    data () {
      this.processCloud()
    }
  },
  methods: {
    processCloud () {
      const list = this.data
      const hover = (item) => this.$emit('item-hover', item)
      const options = Object.assign({
        color: (word, weight) => `hsl(${
          (125 + weight) * 5}, 95%, ${
          100 - (100 - weight) / 4}%)`,
        backgroundColor: 'transparent',
        ellipticity: 0.382,
        minRotation: -0.1,
        maxRotation: 0.1,
        rotateRatio: 0.25,
        shrinkToFit: true,
        wait: 5,
        hover,
        list
      }, this.options)

      if (!list[0][2]) {
        for (let e of list) e[2] = e[1]
      }

      const maxWeight = list.reduce((max, el) => {
        return Math.max(max, el[2])
      }, -Infinity)

      const scale = list.length === 200 ? 100 : 90
      for (let e of list) {
        e[1] = Math.max(scale * Math.pow(e[2] / maxWeight, 0.75), 0)
      }

      const canvas = this.$el
      canvas.width = canvas.offsetWidth
      canvas.height = canvas.width * 0.382

      WordCloud(canvas, options)
    }
  }
}
</script>



